:root {
  --background: #000000;
  --foreground: #fafafa;
  --card: #0a0a0a;
  --card-hover: #141414;
  --card-border: #1f1f1f;
  --card-border-hover: #2a2a2a;
  --primary: #8b5cf6;
  --primary-hover: #a78bfa;
  --primary-foreground: #ffffff;
  --secondary: #1f1f1f;
  --secondary-hover: #2a2a2a;
  --secondary-foreground: #fafafa;
  --accent-pink: #ec4899;
  --accent-blue: #3b82f6;
  --accent-green: #22c55e;
  --accent-orange: #f97316;
  --accent-cyan: #06b6d4;
  --muted: #404040;
  --muted-foreground: #a1a1a1;
  --success: #22c55e;
  --warning: #eab308;
  --error: #ef4444;
  --border: #1f1f1f;
  --ring: #8b5cf6;
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-2xl: 1.5rem;
  --radius-full: 9999px;
}
* {
  border-color: var(--border);
}
html {
  scroll-behavior: smooth;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-geist-sans), system-ui, -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::selection {
  background: var(--primary);
  color: var(--primary-foreground);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--background);
}
::-webkit-scrollbar-thumb {
  background: var(--muted);
  border-radius: var(--radius-full);
}
::-webkit-scrollbar-thumb:hover {
  background: var(--muted-foreground);
}
:focus-visible {
  outline: 2px solid var(--ring);
  outline-offset: 2px;
}
.text-gradient {
  background: linear-gradient(135deg, var(--foreground) 0%, var(--muted-foreground) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.text-gradient-primary {
  background: linear-gradient(135deg, var(--primary) 0%, var(--accent-pink) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.glow {
  box-shadow: 0 0 20px rgba(139, 92, 246, 0.3);
}
.glow-lg {
  box-shadow: 0 0 40px rgba(139, 92, 246, 0.4);
}
.glass {
  background: rgba(10, 10, 10, 0.8);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}
.mac-dots {
  display: flex;
  gap: 8px;
}
.mac-dots span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.mac-dots span:nth-child(1) {
  background: #ff5f56;
}
.mac-dots span:nth-child(2) {
  background: #ffbd2e;
}
.mac-dots span:nth-child(3) {
  background: #27ca3f;
}
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(5deg);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.8;
  }
}
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulse-glow 3s ease-in-out infinite;
}
.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 8s ease infinite;
}
